@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
body{box-sizing:border-box;background-color:#202c37;color:#fff;font-family:"Nunito Sans",sans-serif;font-size:16px}body.light{background-color:#fafafa;color:#111517}body.light .navlogo__header{background-color:#ede3e3}body.light .countryscreen__btn{background-color:#ede3e3;color:#111517}body.light .countryscreen__link-border{background-color:#ede3e3;color:#111517}body.light .homepage__card{background-color:#ede3e3;color:#111517}body.light .search__options-continents{background-color:#ede3e3;color:#111517}body.light input{background-color:#ede3e3;color:#111517}*,*::after,*::before{box-sizing:inherit;margin:0;padding:0}.navlogo__header{align-items:center;box-shadow:1px 1px 10px #0e0d0d;background-color:#2b3945;display:flex;justify-content:space-between;width:100%}.navlogo__header h1{margin-left:1rem;padding:1rem}.navlogo__btn{cursor:pointer;margin-right:1rem;padding:1rem}*{transition:all .3s ease}.homepage__container{align-items:center;display:flex;flex-direction:column;justify-content:center;width:100%}.homepage__cards-container{align-items:center;display:flex;flex-wrap:wrap;grid-gap:2rem;gap:2rem;justify-content:space-around;margin-top:1rem;width:90%}.homepage__card{box-shadow:1px 1px 10px #1b1919;background-color:#2b3945;border-radius:5px;color:#fff;height:auto;letter-spacing:.6px;margin-bottom:3rem;overflow:hidden;padding-bottom:1rem;text-decoration:none;width:240px}.homepage__card img{border-top-left-radius:5px;border-top-right-radius:5px;height:160px;width:100%}.homepage__card h4{font-size:1.1rem;margin:1rem}.homepage__card p{font-size:.8rem;font-weight:100;margin-left:1rem;margin-top:.2rem}.homepage__card p span{font-weight:600}.homepage__card:hover img{-webkit-transform:scale(1.2);transform:scale(1.2)}.countryscreen__container{width:100%}.countryscreen__btn{border:none;border-radius:5px;box-shadow:1px 1px 10px #1b1919;background-color:#2b3945;color:#fff;cursor:pointer;font-size:1rem;margin:2rem;padding:.5rem 1.5rem;text-transform:uppercase}.countryscreen__btn:hover{box-shadow:1px 1px 50px #1b1919}.countryscreen__btn i{margin-right:.5rem}.countryscreen__body-img-text{display:grid;grid-template-columns:1fr 1fr}.countryscreen__img-container{align-items:center;display:flex;justify-content:center;padding:1rem}.countryscreen__img-container img{box-shadow:1px 1px 10px #1b1919;width:90%}.countryscreen__text-container{align-items:center;display:flex;flex-direction:column;justify-content:center;padding:2rem}.countryscreen__country-info{padding:1rem}.countryscreen__country-info h2{font-size:2.5rem}.countryscreen__country-p{-webkit-column-count:2;column-count:2;margin:2rem 0;letter-spacing:1px}.countryscreen__country-p span{font-weight:800}.countryscreen__country-border{display:grid;grid-template-columns:auto 1fr;padding:1rem}.countryscreen__country-border span{font-weight:800}.countryscreen__links{display:flex;flex-wrap:wrap;grid-gap:.5rem;gap:.5rem}.countryscreen__link-border{background-color:#2b3945;border-radius:5px;box-shadow:1px 1px 10px #1b1919;color:#fff;margin:0 .5rem;padding:.2rem .6rem;text-decoration:none}.countryscreen__link-border:hover{box-shadow:1px 1px 50px #1b1919}@media screen and (max-width: 769px){.countryscreen__body-img-text{display:grid;grid-template-columns:0 0;display:flex;flex-direction:column}}.search__container{width:100%;display:flex;justify-content:space-between;align-items:center;padding:2rem}.search__input-container input{color:#fff;border:none;background-color:#2b3945;font-size:1rem;padding:1rem 3rem;border-radius:5px;box-shadow:1px 1px 50px #1b1919}.search__input-container input:focus{outline:none}.search__input-container button{cursor:pointer;background-color:transparent;border:none;font-size:1.9rem;justify-content:center;padding:.5rem}.search__continents select{border-radius:10px;cursor:pointer;box-shadow:1px 1px 50px #1b1919}.search__continents .search__options-continents{cursor:pointer;border:none;background-color:#2b3945;color:#fff;padding:1rem;font-size:1rem}.search__continents .search__options-continents:focus{outline:none}@media screen and (max-width: 500px){.search__container{flex-direction:column}.search__options-continents{margin-top:1.5rem}button{display:none}}.loading__country-screen{width:100%;display:flex;justify-content:center;align-items:center}.lds-roller{margin-top:5rem;display:inline-block;position:relative;width:80px;height:80px}.lds-roller div{-webkit-animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;-webkit-transform-origin:40px 40px;transform-origin:40px 40px}.lds-roller div:after{content:" ";display:block;position:absolute;width:7px;height:7px;border-radius:50%;background:#fff;margin:-4px 0 0 -4px}.lds-roller div:nth-child(1){-webkit-animation-delay:-0.036s;animation-delay:-0.036s}.lds-roller div:nth-child(1):after{top:63px;left:63px}.lds-roller div:nth-child(2){-webkit-animation-delay:-0.072s;animation-delay:-0.072s}.lds-roller div:nth-child(2):after{top:68px;left:56px}.lds-roller div:nth-child(3){-webkit-animation-delay:-0.108s;animation-delay:-0.108s}.lds-roller div:nth-child(3):after{top:71px;left:48px}.lds-roller div:nth-child(4){-webkit-animation-delay:-0.144s;animation-delay:-0.144s}.lds-roller div:nth-child(4):after{top:72px;left:40px}.lds-roller div:nth-child(5){-webkit-animation-delay:-0.18s;animation-delay:-0.18s}.lds-roller div:nth-child(5):after{top:71px;left:32px}.lds-roller div:nth-child(6){-webkit-animation-delay:-0.216s;animation-delay:-0.216s}.lds-roller div:nth-child(6):after{top:68px;left:24px}.lds-roller div:nth-child(7){-webkit-animation-delay:-0.252s;animation-delay:-0.252s}.lds-roller div:nth-child(7):after{top:63px;left:17px}.lds-roller div:nth-child(8){-webkit-animation-delay:-0.288s;animation-delay:-0.288s}.lds-roller div:nth-child(8):after{top:56px;left:12px}@-webkit-keyframes lds-roller{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-roller{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
